.about_backdrop {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .3s linear, visibility .3s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.about_backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

