  width: 100vw;
  max-width: 280px;
  max-height: 460px;
  background: #fff;
  border-radius: 10px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 28px;
  transition: transform .3s;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.about_backdrop.is-hidden .about_modal-window {
  transform: translate(-50%, -50%)scale(0);
}

